export default function rfreshCanvasAnimationFrame() {
  const canvasAnimationFrame = document.getElementById('canvas-play-result');
  const contextCanvasAnimationFrame = canvasAnimationFrame.getContext('2d');
  const frameRate = document.getElementById('frame_rate');
  const showFpsValue = document.querySelector('.fps-value');

  function runAnimation(fps) {
    function startAnimation(img) {
      contextCanvasAnimationFrame.drawImage(img, 0, 0, 250, 250);
    }
    let counter = 0;
    setInterval(() => {
      const list = document.getElementsByTagName('li');
      const img = document.getElementById(list[counter % list.length].children[0].children[1].id);
      startAnimation(img);
      counter += 1;
    }, 1000 / fps);
  }

  frameRate.addEventListener('mouseup', () => {
    runAnimation(frameRate.value);
    showFpsValue.children[0].innerHTML = frameRate.value;
  });
}
