import startDraw from './components/controller/StartDraw';
import colorPen from './components/tool/ColorPen';
import Frames from './components/controller/Frames';
import SizeCanvas from './components/controller/SizeCanvas';
import setBgForAllCanvas from './components/controller/SetBacgroundForAllCanvas';
import objFramesWithImg from './components/data/ObjFramesWithImg';
import objFramesWithDataImg from './components/data/ObjFramesWithDataImg';
import rfreshCanvasAnimationFrame from './components/controller/Animation-frame';


import './screens/styles/styles.css';
import './screens/styles/normalize.css';

setBgForAllCanvas();
const sizeCanvas = new SizeCanvas();
sizeCanvas.size();
const frames = new Frames();
frames.selectFrame();
frames.addFrames();
frames.dellFrame();
colorPen();
startDraw();
const canvas = document.getElementById('main-canvas');
const context = canvas.getContext('2d');
const img = document.getElementById('main-canvas');
const canvasActiveFrame = document.getElementById('201').id;
const imgData = context.getImageData(0, 0, 640, 640);
objFramesWithImg[canvasActiveFrame] = { img };
objFramesWithDataImg[canvasActiveFrame] = imgData;
frames.watchForFrames();
rfreshCanvasAnimationFrame();
