export default function eraserTools() {
  const eraserButton = document.getElementById('eraser-tool');
  const canvas = document.getElementById('main-canvas');
  const context = canvas.getContext('2d');
  const prevColor = document.getElementById('prevColor');


  function eraserCanvas() {
    function startEraser(event) {
      context.fillStyle = prevColor.value;
      context.fillRect(event.layerX, event.layerY, 20, 20);
    }
    canvas.addEventListener('mousedown', startEraser, false);
  }
  eraserButton.addEventListener('click', eraserCanvas);
}
